@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
*{
  margin: 0;
  padding:0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
/* #root{
  height: 100vh;
  width: 100vw;
} */

button{
    outline: none;
    padding: 5px 8px;
    cursor: pointer;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    text-transform: capitalize;
    font-size: 1.2rem;
    letter-spacing: 2px;
    font-family: 500;
    color: black;
    background-color: #fff;
    border: 1px solid rgb(51, 45, 45);
}
.nav{
    width: 100%;
    height: 10vh;
    display: grid;
    place-items: center;
    color: white;
    background-color: #3244ab;
}
.nav h1{
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-weight: 700;
}
.card{
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    background-color: white;
    /* width: 20%; */
    box-shadow: 18px 26px 19px -6px #DDDDDD;
    -webkit-box-shadow: 18px 26px 19px -6px #838076;
    -moz-box-shadow: 18px 26px 19px -6px #838076;
    -o-box-shadow: 18px 26px 19px -6px #838076;
    transition: all 0.6s cubic-bezier(0.175, 0.285, 0, 1);
    border-radius: 10px;
    margin: 5%;
}
.card:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    transition: 0.4s cubic-bezier(0.075, 0.82, 0.165, 1);
}
.card-image{
    width: 100%; 
}
img{
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  width: 100%;
  height: auto;
}
.desc{
    padding: 1rem 1.2rem;
    letter-spacing: 1px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
h3{
    padding: 0.5rem 0rem;
}
p{
    font-size: 1rem;
    font-family: 'Roboto', sans-serif;
}
@media screen and (min-width:320px){
    .box{
        width: 100%;
        display: flex;
    }
    .card h1{
        font-size: 1.7rem;
        white-space: wrap;
    }
    img{
        width: 100%;
    }
    .card{
        flex: 0 0 100%;
        white-space: wrap;
    }
    button{
        font-size: 1rem;
    }
}
@media screen and (max-width:576px){
    .box{
        display: flex;
        align-items: center;
        flex-direction: column;
    }
    .card{
        flex: 0 0 100%;
        height: auto;
    }
}
@media screen and (max-width:768px){
    .box{
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        flex-direction: column;
    }
    .card{
        width: 50%;
        flex: 1 1 50%;
        height: auto;
    }
}
@media screen and (min-width:769px) and (max-width:1200px){
    .box{
        display: flex;
        justify-content: space-evenly;
        flex-wrap: wrap;
        flex-direction: row;
    }
    button{
        white-space: wrap;
    }
    .card{
        flex: 1 0 35%;
        height: -webkit-max-content;
        height: max-content;
    }
}

@media screen and (min-width:1201px){
    .box{
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: row;
        flex-wrap: wrap;
    }
    .card{
        width: 30%;
        flex: 0 1 30%;
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
    }
    button{
        font-size: 1.3rem;
        font-weight: 0;
    }
}

